<template>
  <div class="note">
    <div class="title">
      <p class="note-title">{{noteModel.title}}</p>
      <button v-on:click="onEdit" class="edit">Редактировать</button>
    </div>
    <p class="note-body">{{noteModel.text}}</p>
  </div>
</template>

<script>
export default {
  name: "Note",
  props: {
    noteModel: Object
  },
  methods: {
    onEdit: function() {
      this.$router.push("/edit/" + this.noteModel.id);
    }
  }
};
</script>

<style>
.note {
  background-color: #eeeeee;
  padding: 5px;
  margin: 5px;
}

.title {
  text-align: left;
  background-color: #cccccc;
  margin: 0px 5px 15px 5px;
  padding: 3px 8px;
  font-size: 14pt;
  font-weight: bold;
}

.edit {
  float: right;
}

.note-title {
  display: inline;
}

.note-body {
  text-align: left;
}
</style>
