<template>
  <div id="app">
    <h1>Заметки-заметочки</h1>
    <router-view class="view"></router-view>
    <button v-on:click="newNote">Создать заметку</button>
    <Note v-for="(note) in notes" v-bind:noteModel="note" v-bind:key="note.id"></Note>
  </div>
</template>

<script>
import Note from "./components/Note.vue";
import {HTTP} from "./utils/WebUtils"; // Что значат фигурные скобки? Без них не работает

export default {
  name: "app",
  components: {
    Note
  },
  data: function() {
    return {
      notes: []
    };
  },
  created() {
    HTTP.get("notes")
      .then(resp => {
        this.notes = resp.data;
      })
      // .catch(function(err) {
      //   alert(err);
      // });
  },
  methods: {
    newNote: function() {
      this.$router.push("/add")
    }
  }
};
</script>

<style>
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  margin-top: 60px;
  padding: 0px 50px;
}
</style>
