<template>
  <div id="container">
    <input id="title" v-model="noteModel.title" :disabled="dsbl" type="text">
    <textarea id="text" v-model="noteModel.text" :disabled="dsbl"></textarea>
    <button v-on:click="onSave">Сохранить</button>
    <button v-on:click="onCancel">Отмена</button>
  </div>
</template>

<script>
import { HTTP } from "../utils/WebUtils";

export default {
    // props: {
    //     inputModel: Object
    // },
  data: function() {
    return {
        noteModel: {},
        dsbl: true
    //   title,
    //   text
    };
  },
  created() {
      if (this.$route.params.id != null) {
          HTTP.get("notes/" + this.$route.params.id).then(resp =>{
              this.noteModel = resp.data;
              this.dsbl = false;
          }).catch(err => {
              alert(err);
          });
      } else {
          this.dsbl = false;
      }
  },
  methods: {
    onSave: function() {
        if (this.$route.params.id != null) {
            this.noteModel.id = this.$route.params.id;
        }
        HTTP.post("notes", this.noteModel).then(() =>{
            this.$router.push("/");
        }).catch(err =>{
            alert(err);
        })
    },
    onCancel: function() {
      this.$router.push("/");
    }
  }
};
</script>

<style>
#container {
  margin: 50px;
}

#title {
  display: block;
  width: 100%;
  margin-bottom: 10px;
}

#text {
  display: block;
  width: 100%;
}
</style>
